.blockTitle{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 30px;
}
.title {
  font-family: 'Montserrat', sans-serif;
  line-height: 1.2;
  letter-spacing: 5px;
  font-weight: 500;
  font-size: 36px;
  text-transform: capitalize;
  color: #f89800;
}
